import { Controller, Action } from '@ali/4ever-cangjie';
import { isUndo, isRedo } from '../actions';
import { undoInput } from '../commands/undoInput';
import { redoInput } from '../commands/redoInput';

export default function onAction(action: Action, controller: Controller, next: Function) {
  if (isUndo(action)) {
    return undoInput(controller);
  }
  if (isRedo(action)) {
    return redoInput(controller);
  }

  return next();
}
